 @media screen and (max-width: 960px) {
    .fa-caret-down{
      display: none;
    }

    .navbar h6 {
      color:  #333333;
    }

   .NavbarItems {
     position: relative;
   }
 
   .nav-menu {
     display: flex;
     flex-direction: column;
     width: 100%;
     height: 90vh;
     position: absolute;
     top: 80px;
     left: -100%;
     opacity: 1;
     transition: all 0.5s ease;
   }
 
   .nav-menu.active {
     background: #242222;
     left: 0;
     opacity: 1;
     transition: all 1s ease;
     z-index: 1;
   }
 
   .nav-links {
     text-align: center;
     padding: 2rem;
     width: 100%;
     display: table;
   }
 
   .nav-links:hover {
     background-color:  #ff8093;;
     border-radius: 0;
   }
 
   .navbar-logo {
     position: absolute;
     top: 0;
     left: 0;
     transform: translate(25%, 50%);
   }
 
   .menu-icon {
     display: block;
     position: absolute;
     top: 0;
     right: 0;
     transform: translate(-100%, 60%);
     font-size: 1.8rem;
     cursor: pointer;
   }
 
   .fa-times {
     color: #fff;
     font-size: 2rem;
   }
 
   .nav-links-mobile {
     display: block;
     text-align: center;
     padding: 1.5rem;
     margin: 2rem auto;
     border-radius: 4px;
     width: 80%;
     background:  #ff8093;;
     text-decoration: none;
     color: #fff;
     font-size: 1.5rem;
   }
 
   .nav-links-mobile:hover {
     background: #fff;
     color:  #ff8093;;
     transition: 250ms;
   }
}
 