.navbar {
  background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
 }
 .navbar h6:hover{
   color:  #ff8093;
 }
 
 .navbar-logo {
   color: #fff;
   justify-self: start;
   margin-left: 20px;
   cursor: pointer;
   text-decoration: none;
   font-size: 2rem;
 }
 
 .fa-firstdraft {
   margin-left: 0.5rem;
   font-size: 1.6rem;
 }
 
 .nav-menu {
   display: grid;
   grid-template-columns: repeat(5, auto);
   grid-gap: 10px;
   list-style: none;
   text-align: center;
   width: 70vw;
   justify-content: end;
   margin-right: 2rem;
 }
 
 .nav-item {
   display: flex;
   align-items: center;
   height: 80px;
 }
 
 .nav-links {
   color: white;
   text-decoration: none;
   padding: 0.5rem 1rem;
 }
 
 .nav-links:hover {
   background-color: #ff8093;
   border-radius: 4px;
   transition: all 0.2s ease-out;
   color: #333333;
 }
 
 .fa-bars {
   color: #fff;
 }
 
 .nav-links-mobile {
   display: none;
 }
 
 .menu-icon {
   display: none;
 }
 
